<template>
    <div class="login">
        <div class="content1">
            <form>
                <div class="content-item">
                    <input class="item-input" placeholder="请输入用户名或邮箱" v-model="loginForm.account"/>
                </div>
                <div class="content-item">
                    <input class="item-input" type="password" placeholder="请输入密码" v-model="loginForm.password"/>
                </div>
                <div class="content-item">
                    <input class="item-verify" placeholder="验证码" v-model="loginForm.verityCode"/>
                    <img class="content-item-verity" @click="handleReset($event)" :src="loginForm.veritySrc" />
                </div>
                <div class="submit" @click="handleSubmit"></div>
            </form>
        </div>
        <div v-show="showPoptip" class="poptip-shade">
            <div class="poptip">
                <p>因您退出后台时间超过24小时，机器人下注行为自动暂停了，点击确定后机器人行为将重新激活！</p>
                <div class="enter" @click="handleGoIndex">
                    <span>确定</span>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            loginForm: {
                account: '',
                password: '',
                verityCode: '',
                veritySrc: '/api/Merchant/MixVerifyCode?time=' + new Date().getTime()
            },
            loading: false,
            showPoptip: false
        }
    },
    beforeRouteEnter (to, from, next) {
        // ...
        next(vm => {
            window.addEventListener('keydown', vm.enterClick);
        })
    },
    beforeRouteLeave (to, from, next) {
        // ...
        let that = this;

        window.removeEventListener('keydown', that.enterClick);
        next();
    },
    methods: {
        handleReset(e) {
            e.target.src = '/api/Merchant/MixVerifyCode?time=' + new Date().getTime();
        },
        enterClick(e) {
            let that = this;

            if (e.keyCode === 13) {
                that.handleSubmit();
            }
        },
        handleGoIndex() {
            let that = this;

            if (localStorage.getItem('userId')) {
                that.$router.push({name: 'index'});
            }
        },
        handleSubmit() {
            let that = this;
            if (!that.loginForm.account) {
                that.$Message.error('请输入账号');
                return;
            }

            if (!that.loginForm.password) {
                that.$Message.error('请输入密码');
                return;
            }

            if (!that.loginForm.verityCode) {
                that.$Message.error('请输入验证码');
                return;
            }

            if (!that.loading) {
                that.loading = true;
                that.axios({
                    url: '/api/Merchant/MerchantLogin',
                    method: 'get',
                    params: {
                        name: that.loginForm.account,
                        password: that.loginForm.password,
                        code: that.loginForm.verityCode
                    }
                }).then(function(data) {
                    if (data.data.Status === 100) {
                        localStorage.setItem('merchantID', data.data.Model.MerchantID);
                        localStorage.setItem('loginName', data.data.Model.MerchantName);
                        localStorage.setItem('userId', data.data.Model.MerchantID);
                        localStorage.setItem('password', that.loginForm.password);
                        localStorage.setItem('roomNum', data.data.Model.RoomNum);
                        localStorage.setItem('safetyCode', data.data.Model.SeurityNo);
                        localStorage.setItem('againVerify', data.data.Model.Status);
                        localStorage.setItem('openVerify', data.data.Model.Status);
                        localStorage.setItem('MarsCurrency', data.data.Model.MarsCurrency);
                        that.setLocalData('MerchantAuthorization', data.data.Model.Key);
                        that.setLocalData('Token', data.data.Model.Token);
                        if (data.data.Model.Tips) {
                            that.showPoptip = true;
                        } else {
                            that.$router.push({name: 'index'});
                        }
                        setTimeout(function() {
                            that.loading = false;
                        }, 1500)
                    } else {
                        that.loading = false;
                        that.$Message.error(data.data.Message);
                    }
                }).catch(function(e) {
                    that.loading = false;
                })
            }
        }
    },
}
</script>

<style lang="scss" scoped>
.login {
    position: absolute;
    top: 0;
    left: 0;

    width: 100%;
    height: 100%;

    background-image: url('../../assets/login/indexbg.jpg');
    background-size: 100% 100%;
    .content1 {
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;

        margin: auto;
        padding: 54px 0;

        width: 390px;
        height: 410px;

        background-image: url('../../assets/login/form_back.png');
        background-size: 100% 100%;
        text-align: center;
        box-sizing: border-box;
        .content-item {
            position: relative;

            margin-bottom: 25px;

            height: 60px;

            text-align: center;
            .item-input {
                position: absolute;
                top: 0;
                left: 100px;

                width: 244px;
                height: 60px;

                border: none;
                border-radius: 20px;
                outline: none;
            }

            .item-verify {
                position: absolute;
                top: 1px;
                left: 100px;

                width: 123px;
                height: 60px;

                border: none;
                border-radius: 20px;
                outline: none;
                z-index: 1;
            }

            .content-item-verity {
                position: absolute;
                top: 12px;
                left: 225px;

                width: 110px;
                height: 40px;

                border: none;
                outline: none;
                z-index: 10;
                cursor: pointer;
            }

            .no-cursor {
                cursor: default;
            }
        }
        .submit {
            display: inline-block;

            margin-top: 5px;

            width: 310px;
            height: 60px;

            background-image: url('../../assets/login/login_back.png');
            background-size: 100% 100%;
            text-align: center;

            cursor: pointer;
        }
    }

    .poptip-shade {
        display: flex;
        align-items: center;
        justify-content: center;

        position: fixed;

        width: 100%;
        height: 100%;

        background-color: rgba(0, 0, 0, .45);
        z-index: 999;
        .poptip {
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: space-between;

            padding: 20px;

            width: 300px;
            height: 160px;

            background-color: #FFFFFF;
            border-radius: 4px;
            box-sizing: border-box;

            .enter {
                width: 80%;
                height: 32px;
                line-height: 32px;

                text-align: center;
                color: #fff;
                background-color: #2db7f5;
                border-color: #2db7f5;
                border-radius: 4px;
            }
        }
    }
}
</style>